<template>
  <form
    @submit.prevent="submit"
    @click.stop
    class="tag-form"
  >
    <input
      class="tag-form__input"
      type="text"
      placeholder="Tag hinzufügen"
      v-model="name"
    >
  </form>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  props: {
    collection: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      name: ''
    }
  },
  computed: {
    ...mapGetters ('tag', {
      getTagsForCollection: 'getTagsForCollection',
    })
  },
  methods: {
    ...mapActions ({
      addTag: 'tag/add'
    }),
    submit () {
      const input = this.name.toLowerCase()
      const collectionId = this.collection.id
      const tags = this.getTagsForCollection(collectionId)
      const retval = tags.findIndex(tag => tag.name === input)

      if (retval === -1) {
        this.addTag({
          collectionId: collectionId,
          name: input
        })

        this.name = ''
      } else {
        this.$popup({ message: `#${input} ist schon vorhaden!` })
      }
    }
  }
}
</script>

<style lang="scss">
  .tag-form {
    display: inline-block;

    &__input {
      font-family: 'Avenir', Helvetica, Arial, sans-serif;
      font-size: 14px;
      padding: 3px 6px;
      border-radius: 3px;
      border: 0;
      outline: none;
      height: 20px;

      &:focus {
        border-color: #2c3e50;
      }
    }
  }
</style>
