<template>
  <font-awesome-icon
    :icon="[prefix, name]"
  />
</template>

<script>
import { library } from '@fortawesome/fontawesome-svg-core'
import { faInstagram, fa500px } from '@fortawesome/free-brands-svg-icons'
import { faPencilAlt, faTags, faCameraRetro, faGlobeAfrica } from '@fortawesome/free-solid-svg-icons'
import { faCheckSquare, faTrashAlt, faClipboard } from '@fortawesome/free-regular-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faClipboard, faCheckSquare, faTrashAlt, faPencilAlt, faTags, faCameraRetro, faInstagram, faGlobeAfrica)
library.add(fa500px)

export default {
  components: {
    FontAwesomeIcon
  },
  props: {
    name: {
      type: String,
      required: true
    },
    prefix: {
      type: String,
      default: 'far'
    }
  }
}
</script>
