import Vue from 'vue'
import Router from 'vue-router'
import Tags from './views/Tags.vue'
import Exif from './views/Exif.vue'
import Grid from './views/Grid.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Tags',
      component: Tags
    },
    {
      path: '/exif',
      name: 'Exif',
      component: Exif
    },
    {
      path: '/grid',
      name: 'Grid',
      component: Grid
    }
  ]
})
